import { animationFrameScheduler, interval, scheduled } from 'rxjs';
import { repeat, map, throttle } from 'rxjs/operators';
export function getAnimationEvent$(frameRate = 16) {
    const $animationEvent = scheduled([animationFrameScheduler.now()], animationFrameScheduler).pipe(repeat(), map((start) => animationFrameScheduler.now() - start), throttle(() => interval(frameRate)));
    return $animationEvent;
}
